import {useState,useEffect} from 'react';
import {createId} from 'lib/createId';
import {useUpdate} from './useUpdate';

const useTags=()=> {
    const [tags, setTags] = useState<{id:number;name:string }[]>([]);
    useEffect(()=>{
       let localTags=JSON.parse(window.localStorage.getItem('tags')|| '[]');
        if(localTags.length === 0){
            localTags = [
                {id:createId(),name:'УАБ'},
                {id:createId(),name: 'жБЪ'},
                {id:createId(), name:'СйЈ'},
                {id:createId(), name:'УАї'}
            ];
        }
        setTags(localTags)
    },[]);
   useUpdate(()=>{
       window.localStorage.setItem('tags',JSON.stringify(tags))
   },tags);
    const findTag = (id:number)=>tags.filter(tag=>tag.id===id)[0];
    const findTagIndex = (id:number)=> {
            let result = -1;
            for (let i = 0; i< tags.length;i++){
                if(tags[i].id ===id){
                    result = i;
                    break;
                }
            }
            return result;
        };
    const updateTag= (id:number,obj: {name:string })=>{
        // УјитЈќСйаУдЂТћ╣уџё tag уџёСИІТаЄ
        const index = findTagIndex(id);
        // Ти▒ТІиУ┤Ю tags тЙЌтѕ░ tagsClone
        const tagsClone = JSON.parse(JSON.stringify(tags));
        // Тіі tagsClone уџёугг index тѕаТјЅ№╝їТЇбТѕљ {id:id, name: obj.name}
        tagsClone.splice(index, 1, {id: id, name: obj.name});
        setTags(tagsClone);
    };
    const deleteTag = (id:number)=>{
        // УјитЈќСйаУдЂтѕауџё tag уџёСИІТаЄ
        const index = findTagIndex(id);
        // Ти▒ТІиУ┤Ю tags тЙЌтѕ░ tagsClone
        const tagsClone = JSON.parse(JSON.stringify(tags));
        // Тіі tagsClone уџёугг index тѕаТјЅ
        tagsClone.splice(index, 1);
        setTags(tagsClone);
    };
    const addTag = () => {
        const tagName = window.prompt('Тќ░ТаЄуГЙтљЇуД░СИ║');
        if (tagName !== null && tagName!== '') {
            setTags([...tags, {id:createId(),name:tagName}]);
        }
    };
    const getName = (id:number)=>{
      const tag = tags.filter(t=>t.id===id)[0];
      return tag? tag.name : '';
    };
    return {
        tags,
        setTags,
        findTag,
        updateTag,
        findTagIndex,
        deleteTag,
        addTag,
        getName
    }
};

export {useTags}
